import React from "react";
import Typewriter from "typewriter-effect";

function type() {
  return (
    <Typewriter
      options={{
        strings: [
          "JAVA Developer",
          "Front End Developer",
          "MERN Stack Developer",
        ],
        autoStart: true,
        loop: true,
        deleteSpeed: 50,
      }}
    />
  );
}
export default type;