import GitHubIcon from '@material-ui/icons/GitHub'
import LinkedInIcon from '@material-ui/icons/LinkedIn'
import { about } from '../../portfolio'
import './About.css'
import Type from "./type";
const About = () => {
  const {photo,name,resume, social } = about

  return (
    <div className='about center'><br/><br/><br/>
      {photo && (
       <img height="200px" align="center" alt="Avatar placeholder" src={photo} />
      )}
      <br/>
      {name && (
        <h1 textAlign="center">
          Hi, I am <span className='about__name'>{name}.</span>
        </h1>
      )}
      
      <div style={{ padding: 50, textAlign: "center",fontSize:"60px",color:"#6495ED",fontWeight:"bold" }}>
                <Type />
              </div>
              <div style={{ paddingTop: 30,marginInlineStart:50,marginInlineEnd:50,textAlign: "center",fontSize:"20px",fontWeight:"bold"}}>
      I am goodatprogramming.Java development and webdevelopment<br/>aremoreofmy interests.<br/>I amlookingforopportunitiestojoina workthatwillallowme<br/>togrowprofessionally,<br/>learnmore,andreachmyfullpotential.<br/>My hobbies are playing chess and reading books
       </div>
      <div className='about__contact center'>
        {resume && (
          <a href={resume}   target="_blank">
            <span type='button' className='btn btn--outline'>
              Resume
            </span>
          </a>
        )}

  {social && (
          <>
            {social.github && (
             <a
                href={social.github}
                aria-label='github'
                className='link link--icon'
                target="_blank" 
              >
                <GitHubIcon />
              </a>
            )}

            {social.linkedin && (
              <a
                href={social.linkedin}
                aria-label='linkedin'
                className='link link--icon'
                target="_blank"
              >
                <LinkedInIcon />
              </a>
            )}
          </>
        )}
      </div>
    </div>
  )
}

export default About